.lightbox {
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  height: 100vh;
  width: 100vw;
  background: #33394CF0;
  z-index: 999;
  transition: 0.2s ease-out;
  cursor: zoom-out;
}
  
.lightbox-img {
  width: fit-content !important;
  height: fit-content !important;
  max-width: 90vw;
  max-height: 90vh;
  margin: auto;
  transition-property: margin top left width height;
  transition-duration: 3s;
}

.zoom {
  cursor: zoom-in;
  position: relative;
}

.overlay {
  position: absolute;
  z-index: 2;
  top: 0;
  opacity: 0;
  color: #fff;
  width: 100%;
  height: 100%;
  transition: .5s ease;
  background-image: url('../../assets/zoom.png');
  background-repeat: no-repeat;
  background-position: center;
}
  
@media (hover: hover) {
  .zoom:hover .overlay {
    opacity: 0.4;
  }
}

img.missing {
  object-fit: scale-down;
  margin: auto;
}