.button-77 {
  align-items: center;
  appearance: none;
  background-clip: padding-box;
  background-color: initial;
  background-image: none;
  border-style: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  flex-direction: row;
  flex-shrink: 0;
  /* font-family: Eina01,sans-serif; */
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
/*   line-height: 20px; */
  margin: 0;
/*   min-height: 64px; */
  outline: none;
  overflow: visible;
  padding: 19px 26px;
  pointer-events: auto;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  word-break: keep-all;
  z-index: 0;
  width: 100%;
}

/* @media (min-width: 768px) {
  .button-77 {
    padding: 19px 32px;
  }
}
 */


.button-77:before,
.button-77:after {
  border-radius: 10px;
}

.button-77:before {
  /* background-color: rgba(249, 58, 19, .32); */
  background-color: #0C7DBF52;
  content: "";
  display: block;
  height: 100%;
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -2;
}

.button-77:after {
  background-color: initial;
  /* background-image: linear-gradient(92.83deg, #ff7426 0, #f93a13 100%); */
  background-image: linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
  bottom: 3px;
  content: "";
  display: block;
  left: 3px;
  overflow: hidden;
  position: absolute;
  right: 3px;
  top: 3px;
  transition: all 100ms ease-out;
  z-index: -1;
}

@media (hover: hover) {
  .button-77:hover:not(:disabled):after {
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    transition-timing-function: ease-in;
  }
}

.button-77:active:not(:disabled) {
  color: #ccc;
}

.button-77:active:not(:disabled):after {
  /* background-image: linear-gradient(0deg, rgba(0, 0, 0, .2), rgba(0, 0, 0, .2)), linear-gradient(92.83deg, #ff7426 0, #f93a13 100%); */
  background-image: linear-gradient(0deg, rgba(0, 0, 0, .2), rgba(0, 0, 0, .2)), linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
  bottom: 4px;
  left: 4px;
  right: 4px;
  top: 4px;
}

.button-77:disabled {
  cursor: default;
  opacity: .24;
}
