* {
  box-sizing: border-box;
}



.inp {
  position: relative;
  margin: auto;
  width: 100%;
  max-width: 250px;
  overflow: hidden;
  border-radius: 10px; 
}

.inp .container {
/*   border-radius: 10px;  */
/*   background: #0000000D;  */
}

.inp .label {
  position: absolute;
  top: 20px;
  left: 12px;
  font-size: 20px;
  /* rgba(0, 0, 0, 0.5); */
  color: #FFFFFFA0; 
/*   color: #FF000080;  */
  font-weight: normal;
  transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transition: all 0.3s ease;
  pointer-events: none;
}

.inp.error .label {
  color: #ff7676;
}


