  font-size: 14px;
}
 */

.inp .focus-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 65px;
  /* rgba(0, 0, 0, 0.05); */
  background: #0000000D; 
  z-index: -1;
  transform: scaleX(1);
  transform-origin: left;
  border-radius: 10px;
}
.inp input {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  width: 100%;
  border: 0;
  font-family: inherit;
  padding: 16px 12px 0 12px;
  height: 65px;
  font-size: 20px;
  font-weight: normal;
  background: #454955; /* rgba(0, 0, 0, 0.02); */
  box-shadow: inset 0 -2px 0 #FFFFFFA0; 
  color: #fff;
  transition: all 0.15s ease;
/*   border-radius: 5px; */
}
.inp input::selection {
  background-color: #878787;
}

.inp.error input {
  box-shadow: inset 0 -2px 0 #ff7676A0;  
}

@media (hover: hover) {
  .inp input:hover {
    background: #0000000A; /*rgba(0, 0, 0, 0.04); */
    box-shadow: inset 0 -3px 0 #FFFFFFFF /* rgba(0, 0, 0, 0.5); */
  }
  .inp.error input:hover {
    box-shadow: inset 0 -2px 0 #ff7676;  
  }
}

/* Make sure label does not ease back when text in (placeholder not shown) */
.inp input:not(:-moz-placeholder-shown) + .label {
  /* rgba(0, 0, 0, 0.5); */
  color: #FFFFFF80; 
  transform: translate3d(0, -12px, 0) scale(0.75);
}

.inp input:not(:-ms-input-placeholder) + .label {
  /* rgba(0, 0, 0, 0.5); */
  color: #FFFFFF80;
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:placeholder-shown) + .label {
   /* rgba(0, 0, 0, 0.5); */
  color: #FFFFFF80;
  transform: translate3d(0, -12px, 0) scale(0.75);
/*   transition: color 3.1 ease; */
}

/* ERROR */
.inp.error input:not(:-moz-placeholder-shown) + .label {
  /* rgba(0, 0, 0, 0.5); */
  color: #ff7676;
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp.error input:not(:-ms-input-placeholder) + .label {
  /* rgba(0, 0, 0, 0.5); */
  color: #ff7676;
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp.error input:not(:placeholder-shown) + .label {
   /* rgba(0, 0, 0, 0.5); */
  color: #ff7676A0;
  transform: translate3d(0, -12px, 0) scale(0.75);
}




.inp input:focus {
  /* rgba(0, 0, 0, 0.05); */
  background: #0000000D;
  
  outline: none;
  box-shadow: inset 0 -3px 0 #76cdff;
  transition: all 0.1s ease;
}
.inp.error input:focus {
  background: #0000000D;
  outline: none;
  box-shadow: inset 0 -3px 0 #FF0000;
  transition: all 0.1s ease;
}
.inp input:focus + .label {
  color: #76cdff;
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp.error input:focus + .label {
  color: #ff7676;
/*   color: #fa3838A0; */
  transform: translate3d(0, -12px, 0) scale(0.75);
}

.inp input:focus + .label + .focus-bg {
  transform: scaleY(1);
  transition: all 0.1s ease;
}


.error-msg {
  display: block;
  width: 100%;
  padding: 10px;
  color: #ff7676A0;
  background-color: #ff767620; 
  font-size: 14px;
  transition: transform 0.2s ease;  
  transform: scaleY(1);
}

.error-msg.hide {
  transition: transform 0.5s ease;  
  transform: translateY(-50%) scaleY(0);
}

