.body {
  background-color: #3b404e;
  display: flex;
  justify-content: center;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}


/************* MAIN CONTAINER *************/

.whats-new {
  font-size: 15px;
}

.whats-new > li {
  padding: 5px 0;
}

.survey-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 90vw;
  max-width: 600px;
}

.survey-container h2 {
  color: #76cdff;
}

/* @media (max-width: 550px) {
  .survey-container {
    width: 500px;
  }
} */


.survey-responses {
  transition: all 1s ease;
  width: 100%;
  padding-bottom: 30px;
}



.survey-options {
}


.survey-question {
  font-size: 20px;
  font-weight: normal;
  padding-bottom: 20px;
}

.survey-response {
  /* padding: 16px; */
}

.survey-response label {
  display: block;
  padding: 15px 30px 15px 10px;
  font-size: 20px;
  background-color: #454955;

  cursor: pointer;  
  border: 1px solid #454955;
  border-radius: 10px;
  margin: 10px 0;
}

@media (hover: hover) {
  .survey-response label:hover {
    /* background-color: #454955; */
    background-image: linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
    border-radius: 10px;
  }
}

.survey-response label:active {
  /* background-color: #0C7DBF; */
  background-image: linear-gradient(0deg, rgba(0, 0, 0, .2), rgba(0, 0, 0, .2)), linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
  border: 1px solid #0C7DBF;

  /* border-radius: 10px; */
}

.survey-response.selected label {
  background-color: #0C7DBF;
  border: 1px solid #76cdff;

  font-weight: bold;
}

.survey-response input[type=radio] {
  margin-right: 20px;
  height: 15px;
  width: 15px;
}

.survey-text {
  display: flex;
  flex-direction: column;
  align-items: center;
}

