.survey-button-next {
  align-self: center;
  /* min-height: 60px; */
  min-width: 95px;
}


.survey-footer {
  padding: 30px 0 50px 0;
  align-self: stretch;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row-reverse;
}

@media (max-width: 300px) {
  .survey-footer {
    flex-direction: column;
  }
}

button.link {
  background: none!important;
  border: none;
  padding: 0!important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #fff;
  font-size: 18px;
  cursor: pointer;
}

@media (hover: hover) {
  button.link:hover {
    text-decoration: underline;
  }
}

.survey-button-back {
  min-width: 95px;
}

.survey-progress {
}

/*********************************************************************************************
 **** SURVEY IMAGE OPTIONS ****
 ********************************************************************************************/

 
.survey-image-options {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.survey-image-option {
  padding: 10px;
  background-color: #454955;
  border: 1px solid #454955;
  border-radius: 10px;
  cursor: pointer;  
  /* height: 61px;
  width: 61px; */
  max-width: 250px;
  /* max-height:155px; */
  color: #fff;
  transition: 0.5s ease;
}

@media (max-width: 570px) {
  .survey-image-option {
    max-width: 200px;
    /* max-height: 120px; */
    transition: 0.5s ease;
  }
}

@media (max-width: 460px) {
  .survey-image-option {
    max-width: 155px;
    /* max-height: 120px; */
    transition: 0.5s ease;
  }
}


.survey-image-option img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

@media (hover: hover) {
  .survey-image-option:hover {
    background-image: linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
    /* border: 1px solid #ffffff; */
  }
}

.survey-image-option:active {
  background-image: linear-gradient(0deg, rgba(0, 0, 0, .2), rgba(0, 0, 0, .2)), linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
  /* border: 1px solid #ffffff; */
  border: 1px solid #0C7DBF;
}

.survey-image-option.selected {
  /* background-image: linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%); */
  background-color: #0C7DBF;
  border: 1px solid #76cdff;
}

@media (hover: hover) {
  .survey-image-option.selected:hover {
    background-image: linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
    border: 1px solid #76cdff;
  }
}

.survey-image-option.selected:active {
  background-image: linear-gradient(0deg, rgba(0, 0, 0, .2), rgba(0, 0, 0, .2)), linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
  border: 1px solid #76cdff;
}



/*********************************************************************************************
 **** SURVEY RANGE ****
 ********************************************************************************************/

.survey-range {
  display: flex;
  min-width: 250px;
  width: 100%;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 15px 0;
  font-size: 20px;
  font-weight: bold;
  gap: 10px;
}

.survey-range-label {
  /* padding: 20px 10px; */
  flex-grow: 1;
  text-align: center;
  font-weight: normal;
  padding-bottom: 15px;
}

.survey-range-options {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  gap: 5px;
}

@media (max-width: 435px) {
  .survey-range-label {
    text-align: center;
  }
}

.survey-range-response-container {
  width: 61px;
}

.survey-range-response {
  /* padding: 20px 25px;
  background-color: #0C7DBF;
  border: 1px solid #0C7DBF;
  border-radius: 50%; */
  font-size: 20px;
  font-weight: normal;
  padding: 15px 23px;
  background-color: #454955;
  border: 2px solid #0C7DBF;
  border-radius: 50%;
  cursor: pointer;  
  height: 61px;
  width: 61px;
  color: #fff;
}

.survey-range-response-desc-container {
  width: 325px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: flex-end;
  gap: 5px;
}
.survey-range-response-desc {
  font-size: 15px;
  font-weight: normal;
  color: #76cdff;
  text-align: center;
}

