.container {
  /* GRID */
  display: grid;
  width: minmax(auto, 1024px);
  /* height: minmax(auto, 720px); */
  grid-template-columns: 100px minmax(120px, 300px) minmax(5px, 20px) minmax(200px, 590px);
  grid-template-rows: 30px repeat(3, 1em) minmax(auto, 580px);
  grid-gap: 0.5rem;
  grid-template-areas:
    "photo    account   . content"
    "photo    friends   . content"
    "photo    followers . content"
    ".        .         . content"
    "post     post      . content"
    "likes    shares    . ."
    /* ".        .         . content" */
  ;
}


.item {
  background-color: #33394C;
/*   background-image: linear-gradient(130deg, #6C52D9 0%, #1EAAFC 85%, #3EDFD7 100%); */
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  color: #ffffff;
  border-radius: 4px;
/*   border: 2px solid #171717; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}


.photo {
  grid-area: photo;

  border: none;
  box-shadow: none;
  background-color: transparent;
  background-image: url("/src/assets/avatar.png");
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
  border-radius: 0px;
}


.account {
  grid-area: account;

  border: none;
  box-shadow: none;
  background-color: transparent;
  justify-content: start;
}

.account p {
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 20px;  
}


.friends {
  grid-area: friends;
}

.followers {
  grid-area: followers;
}

.friends,
.followers {
  border: none;
  box-shadow: none;
  background-color: transparent;
  justify-content: start;
  font-size: 14px;  
}

.friends span:before {
  content: "ðŸ‘¥ "
}

.followers span:before {
  content: "ðŸ—£ "
}


.post {
  grid-area: post;

  border: none;
  padding: 10px;
}

.post img {
  width: 100%;
  height: 100%;
  object-fit: contain;  
}


.likes, .shares, .views {
  border: none;
  box-shadow: none;
  background-color: transparent;  
  overflow: hidden;
  white-space: nowrap;
  align-self: start;
  justify-content: start;
  text-overflow: clip;
  font-size: 14px;
}

.likes {
  grid-area: likes;
}

.likes span:before {
  content: "ðŸ‘ "
}

.shares {
  grid-area: shares;
  justify-content: end;  
}

.views {
  grid-area: views;
}


.content {
  grid-area: content;
  align-items: start;
  padding-bottom: 10px;
}



/************* MAIN CONTAINER > CONTENT CONTAINER *************/

.content-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
  /* grid-template-columns: 1fr; */
  /* grid-template-rows: 1fr 1fr; */
  /* grid-gap: 0.5rem; */
  /* grid-template-areas:
    "messages"
    "responses"
  ; */
  gap: 1rem;
  /* padding: 10px; */
  /* border: 1px solid red; */
  overflow-x: hidden;
  overflow-y: scroll;
}

.content-container-bottom {
  justify-content: flex-end;
}

.content-container * {
  overflow-anchor: none;
}

.overflow-anchor {
  height: 1px;
  overflow-anchor: auto;
}


.content-item {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.messages {
  /* grid-area: messages; */

  border: none;
  box-shadow: none;
  padding: 10px;
  justify-content: flex-start;
}

.responses {
  /* grid-area: responses; */

  justify-content: flex-end;
  border: none;
  box-shadow: none;
  padding: 10px;
}


/************* MAIN CONTAINER > CONTENT CONTAINER > MSG CONTAINER *************/

.msg-container {
  display: grid;
  /* width: 100%; */
  /* height: 100%; */
  grid-template-columns: 30px auto;
  grid-template-rows: minmax(20px, auto);
  grid-auto-rows: minmax(50px, auto);
  grid-gap: 0.3rem;
  max-width: 95%;
/*   grid-template-areas:
    "msg-photo msg-1"
    ".         msg-2"
    ".         msg-3"; */
}


.msg-item {
  background-color: #3b404e;
/*   background-image: linear-gradient(130deg, #6C52D9 0%, #1EAAFC 85%, #3EDFD7 100%); */
/*   box-shadow: 
    0 3px 6px rgba(0,0,0,0.10), 
    0 3px 3px rgba(0,0,0,0.13)
  ; */
  color: #ffffff;
  border-radius: 4px;
  border: 1px solid #373737;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}


.msg-photo {
  grid-column: 1;
  grid-row: 2 / span 2;

  border: none;
  box-shadow: none;
  background-color: transparent;
  background-image: url("/src/assets/expert.png");
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
  border-radius: 0px;
}

.msg-name {
  grid-column: 2;
  grid-row: 1;

  border: none;
  background: none;
  box-shadow: none;
  justify-content: start;
  align-items: center;
  font-size: 14px;
  padding-left: 5px;
  font-weight: bold;
}

.msg {
  grid-column: 2;

  padding: 0px 10px;
  justify-content: start;
  align-items: center;
  font-size: 16px;
}

.msg p {
  /* display: block;
  width: 100%; */
}


/************* MAIN CONTAINER > CONTENT CONTAINER > RESPONSE CONTAINER *************/

.res-container {
  display: grid;
  /* width: 100%; */
  /* max-width: 95%; */
  /* height: 100%; */
  grid-template-columns: auto 30px;
  grid-template-rows: minmax(20px, auto);
  grid-auto-rows: minmax(50px, auto);
  grid-gap: 0.5rem;
/*   grid-template-areas:
    "msg-photo msg-1"
    ".         msg-2"
    ".         msg-3"; */
}


.res-item {
  background-color: #3b404e;
/*   background-image: linear-gradient(130deg, #6C52D9 0%, #1EAAFC 85%, #3EDFD7 100%); */
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  color: #ffffff;
  border-radius: 4px;
  border: 1px solid #878787;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  padding: 10px;
}

.res-name {
  grid-row: 1;

  border: none;
  background: none;
  box-shadow: none;
  justify-content: end;
  align-items: center;
  font-size: 14px;
  padding-left: 5px;
  font-weight: bold;
}


.res-photo {
  grid-column: 2;
  grid-row: 2 / span 1;

  border: none;
  box-shadow: none;
  background-color: transparent;
  background-image: url("/src/assets/avatar.png");
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
  border-radius: 0px;

}


.res-prompt {
  grid-column: 1;
  grid-row: 1;

  border: none;
  background: none;
  box-shadow: none;
  justify-content: start;
  align-items: end;
  font-size: 16px;
  padding-left: 5px;
  font-weight: bold;
  font-style: italic;
}


.response {
  grid-column: 1;
/*   grid-row: 2; */

  justify-content: center;
  text-align: center;
  background-color: #0C7DBF;  
  cursor: pointer;  
}

@media (hover: hover) {
  .response:hover {
    background-image: linear-gradient(130deg, #0CBFA7 0%, #1EAAFC 85%, #3EDFD7 100%); 
    font-weight: bold;
  }
}

.res-msg {
  grid-column: 1;
/*   grid-row: 2; */

  max-width: 90%;
  justify-content: end;
  text-align: left;
  background-color: #0C7DBF;  
}

.res-msg img, .msg img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;  
  padding: 10px 0;
}

.hidden {
  display: none;
}

.options-container {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}
